/**
 * Thing model events
 */

'use strict';

import {EventEmitter} from 'events';
import Stocks from './stocks-api.model';
var StocksEvents = new EventEmitter();

// Set max event listeners (0 == unlimited)
StocksEvents.setMaxListeners(0);

// Model events
var events = {
  save: 'save',
  remove: 'remove'
};

// Register the event emitter to the model events
for(var e in events) {
  let event = events[e];
  Stocks.schema.post(e, emitEvent(event));
}

function emitEvent(event) {
  return function(doc) {
    StocksEvents.emit(`${event}: ${doc._id}`, doc);
    StocksEvents.emit(event, doc);
  };
}

export default StocksEvents;
